



@media only screen and (min-width: 1800px) {

    .App {
        display: none;
    }

 
    

}





/* for (mobile) landscapes: */
@media screen and (max-width: 1800px) and (orientation: landscape) { 


    .App {
        flex-direction: row-reverse;
        align-items: flex-start;

        height: 100vh;



     
 

    }






/* holds photo and navbar: */
    .App > div { 
        width: 30%;

        position: sticky;
        top: 0;

        

    }

    .App > div > h1 {
        display: none;
    }





    #Bio, #Calendar, #Awards { 
     

        width: 70%;

        border-radius: 0px;
        align-self: stretch;
  


    }




  #Bio > h1, #Calendar > h1, #Awards > h1 { 
      display: block;



  }








    #photo img {

        width: 60%;
    }










    #navBar { 
        flex-direction: column;
    }



    #navBar > * { 
        width: 100%;
        border-radius: 7px;

        padding: 17px 0;


        margin-top: 5px;
        margin-right: 50px;
    }









 

