


h1 {
    margin: 0 0 10px 0;
    font-family: var(--header1);
    font-size: 50px;
}

h2 {
    font-family: var(--header2);
    font-size: 30px;
    color: white;
    text-shadow: 2px 2px 3px #646464;
}

p {
    font-family: var(--para);
    font-size: 30px;
}


.App {

    height: 95vh;



    padding: 0 0;
    text-align: center;
    background-color: rgba(255, 255, 0, 0);

    display: flex;
    flex-direction: column;

    align-items: center;



}










#photo {

    width: 100%;
    margin-top: 10px;
    background-color: rgba(255, 0, 0, 0);
 


}


#photo img {
    width: 30%;
    height: auto;
    border-radius: 50%;

}












#navBar {

    width: 75%;


    display: flex;
    flex-direction: row;
    justify-content: center;





}



#navBar > * {

    border-radius: 7px 7px 0 0;
    border: 0;
    padding: 15px 0;

    width: 30%;
    margin: 0 1%;

    outline: none;
    font-family: var(--buttons);
    font-size: 20px;
    font-weight: bold;


  

}


#BioBTN {
    background-color: rgb(132, 236, 100);
    
}


#CalendarBTN {
    background-color: rgb(152, 210, 243);

}

#AwardsBTN {
    background-color: rgb(243, 130, 130);


}








.App > div {
    background-color: rgba(177, 72, 72, 0);

    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
}




/* content areas: */
#Bio, #Calendar, #Awards {
    width: 85%;
    border-radius: 0px 0px 8px 8px;
    padding: 5%;

    flex-grow: 1;

  
}



#Bio > h1, #Calendar > h1, #Awards > h1 { 
    display: none;
}


#Bio {
    background-color: rgb(132, 236, 100);
}



#Calendar {
    background-color: rgb(152, 210, 243);
}


#Awards {
    background-color: rgb(243, 130, 130);
}
